import Vue from 'vue'
import Router from 'vue-router'
import Layout from '@/pages/Layout'
import _ from 'lodash';

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Layout',
      component: Layout
    }
  ]
})



